.header {
	display: flex;
	justify-content: space-between;
	align-items: center;
	border-bottom: 1px solid whitesmoke;
}
.header__left{
    display: flex;
    align-items: center;
}
.header__left > img {
	object-fit: contain;
	height: 80px;
	margin-left: 5px;
}
.header__middle{
    display: flex;
    flex:0.7;
    align-items: center;
    background-color: whitesmoke;
    border-radius: 5px;
  
}
.header__middle> .MuiSvgIcon-root{
    color: grey;
}
.header__middle > input{
    border: none;
    width: 100%;
    padding: 10px;
    outline:none;
    font-size: medium;
    background-color: transparent;
}
.header__right{
    display: flex;
    /* justify-content: space-between; */
    padding-right: 20px;;
}